/* Have to set height explicity on ui-view
to prevent collapsing during animation*/
.ui-view-container {
  position: relative;
  height: 100%; }

[ui-view].ng-enter, [ui-view].ng-leave {
  position: absolute;
  left: 0;
  right: 0;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

[ui-view].ng-enter {
  opacity: 0;
  -webkit-transform: scale3d(0.8, 0.8, 0.8) translate3d(-100px, 100px, 1000px);
  -moz-transform: scale3d(0.8, 0.8, 0.8) translate3d(-100px, 100px, 1000px);
  transform: scale3d(0.8, 0.8, 0.8) translate3d(-100px, 100px, 1000px); }

[ui-view].ng-enter-active {
  opacity: 1;
  -webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
  -moz-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
  transform: scale3d(1, 1, 1) translate3d(0, 0, 0); }

[ui-view].ng-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

[ui-view].ng-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.sample-show-hide {
  transition: all linear 0.4s; }

.sample-show-hide.ng-hide {
  opacity: 0;
  transform: translate3d(1000px, -1000px, 500px) scale3d(0.1, 0.5, 1); }

.header-show-hide {
  transition: all linear 400ms; }

.header-show-hide.ng-hide {
  opacity: 0;
  transform: translate3d(1000px, 0, 0) scale3d(0.1, 1, 1); }
